<template>
	<view class="main" :style="{height:screenHeight + 'px', width:screenWidth + 'px'}" @touchmove.prevent>
		<view style="padding-bottom: 50px;padding-top: 20px;">
			<checkbox-group @change="checkboxChange">
				<view v-for="item in problemList" :key="item.index" class="problemList">
					<view class="problemList-content">
						<checkbox class="checkbox" :value="item.id" :checked="item.check"></checkbox>
						<view>
							<view style="width: 120%;">{{item.project}}/{{item.vr}}</view>
							<view style="color: #6d6a6a;width: 250px;word-wrap: break-word;overflow-wrap: break-word;">{{item.problemDescription}}</view>
						</view>
					</view>
					<view class="problemList-sign">
						<text>{{item.generalDefect}}</text>
					</view>
				</view>
			</checkbox-group>
			<view class="bottom-button">
				<button class="bottom-button-save" @click="saveButton">Save</button>
				<button class="bottom-button-new" @click="newProblemHandle">New Problem</button>
			</view>
		</view>
		
	</view>
</template>
<script>
    import {
        forEach
    } from "../../common/luch-request/utils";
    import { API } from '@/api/api';
	export default {
		data() {
			return {
				screenWidth: '',
				screenHeight: '',
				selectList: [],
				problemList: [],
				checkItemData: null, //结果详情数据
				resultData: null, //测试结果数据
				scrollTop:0.01
			}
		},
		created() {
			
		},
		onLoad(e) {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			    plus.navigator.setStatusBarStyle("dark")
			}
			if (e.checkItemData != undefined && e.checkItemData != null && e.checkItemData != null) {
				this.checkItemData = JSON.parse(decodeURIComponent(e.checkItemData))
			}
			if (e.resultData != undefined && e.resultData != null && e.resultData != null) {
				this.resultData = JSON.parse(e.resultData)
			}
			if(e.scrollTop){
				this.scrollTop = e.scrollTop
			}
			this.getScreenList()
			this.getProblemList()
		},
		onShow() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
			    plus.navigator.setStatusBarStyle("dark")
			}
		},
		methods: {
			getProblemList() {
				let data = this.checkItemData
				let params = {
					tqs: data.tqs,
					project: data.project,
					checkItems: data.checkItems,
					itemId: data.itemId,
					part: data.part,
					resultSubtableId: data.id

				}
                this.$http.get(API.testCase.qaTestProblem_appSelectProblemList, {
                    params: params
                }).then(res => {
					if (res.data.success) {
						this.problemList = res.data.result.problemList
						this.selectList = res.data.result.selectList
						this.problemList.forEach(item => {
							if (this.selectList) {
								if (this.selectList.indexOf(item.id) != -1) {
									item.check = true
								} else {
									item.check = false
								}
							} else {
								item.check = false
							}
						})
					}
				})
			},
			saveButton() {
				let that = this
				if (that.selectList && that.selectList.length > 0) {
					if (that.problemList && that.problemList.length > 0) {
						//勾选问题的整条数据
						let newList = that.problemList.filter(function(item) {
							return that.selectList.indexOf(item.id) != -1
						})
						newList.forEach((item) => {
							//结果id
							if (that.checkItemData.resultId) {
								item.resultId = that.checkItemData.resultId
							} else {
								item.resultId = that.resultData.id
							}
							//结果详情id  对应  checkItem项
							item.resultSubtableId = that.checkItemData.id
						})
						that.checkItemData.list = newList
                        that.$http.post(API.testCase.qaTestResultSubtableProblem_appSelectProblem, that.checkItemData)
                            .then(res => {
								if (res.data.success) {
									uni.showToast({
										title: 'Save Success'
									})
									uni.navigateTo({
										url: '../task/checkItems?data=' + JSON.stringify(that.resultData) 
																		+'&scrollTop=' + that.scrollTop
									});
								}
							})
					}
				} else {
					uni.showToast({
						icon:'none',
						title: 'Please Select At Least One Problem'
					})
					return
				}
				// uni.navigateBack();
			},
			getScreenList() {
				let res = uni.getSystemInfoSync();
				this.screenWidth = res.screenWidth; // 获取屏幕宽度
				this.screenHeight = res.screenHeight; // 获取屏幕高度
			},
			checkboxChange(value) {
				this.selectList = value.detail.value
			},
			newProblemHandle() {
				let that = this
				if (that.selectList && that.selectList.length > 0) {
					if (that.problemList && that.problemList.length > 0) {
						//勾选问题的整条数据
						let newList = that.problemList.filter(function(item) {
							return that.selectList.indexOf(item.id) != -1
						})
						newList.forEach((item) => {
							//结果id
							if (that.checkItemData.resultId) {
								item.resultId = that.checkItemData.resultId
							} else {
								item.resultId = that.resultData.id
							}
							//结果详情id  对应  checkItem项
							item.resultSubtableId = that.checkItemData.id
						})
						uni.navigateTo({
							url: '../task/newProblem?selectList=' + JSON.stringify(newList) + '&checkItemData=' +
								encodeURIComponent(JSON.stringify(this.checkItemData)) + '&resultData=' + JSON.stringify(this
									.resultData) + '&scrollTop=' + that.scrollTop
						});
					}
				} else {
					let newList = []
					uni.navigateTo({
						url: '../task/newProblem?selectList=' + JSON.stringify(newList) + '&checkItemData=' +
							JSON.stringify(this.checkItemData) + '&resultData=' + JSON.stringify(this
								.resultData) + '&scrollTop=' + that.scrollTop
					});
				}
			}
		}
	}
</script>
<style scoped scoped>
	.main {
		padding: 20px;
		box-sizing: border-box;
		padding-top: env(safe-area-inset-top); /* 兼容 iOS 11.0-11.4 */
		padding-top: constant(safe-area-inset-top); /* 兼容 iOS 12.0-12.1 */
	}

	.problemList {
		display: flex;
		box-sizing: border-box;
		justify-content: space-between;
		border-radius: 5px;
		padding: 10px;
		width: 100%;
		height: 13%;
		margin-bottom: 5%;
		background: white;
	}

	.problemList-content {
		display: flex;
	}

	.checkbox {
		margin-right: 3%;
	}

	.problemList-sign {
		display: flex;
		align-items: center;
		justify-content: center;
		background: #1890ff4f;
		width: 24%;
		height: 30px;
		color: #1890FF;
		border-radius: 10%;
		border: 1px solid #1890FF;
	}

	.bottom-button {
		display: flex;
		background: white;
		padding: 3% 3%;
		border-radius: 2%;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		box-sizing: border-box;
	}

	.bottom-button-save {
		width: 50%;
		margin: 0 2%;
		background: #ea3800a3;
		color: white;
	}

	.bottom-button-new {
		width: 50%;
		margin: 0 2%;
		background: #090c1c;
		color: white;
	}
</style>
